<template>
  <div>
    <b-container fluid :style="cssVars"> 

    

 <!-- Carrusel -->

 <b-row>



   <b-carousel
      id="carousel-1"
      v-model="slide"
      :interval="4000"
      controls
      indicators
      background="#ababab"
      img-width="1200"
      img-height="500"
      style="text-shadow: 1px 1px 2px #333; padding:0;"
      label-next=""
      label-prev=""
      @sliding-start="onSlideStart"
      @sliding-end="onSlideEnd"
      v-if="banners.data"
    >

      <!-- Slides with img slot -->
      <!-- Note the classes .d-block and .img-fluid to prevent browser default image alignment -->
      <b-carousel-slide v-for="banner in banners.data.data.banners" :key="banner.id">
        <template #img>
          <img
            class="d-block img-fluid w-100"
            width="1200"
            height="500"
            :src="url+banner.imagen"
            alt=""
          >
        </template>
      </b-carousel-slide>
     
    </b-carousel>

     </b-row>

<!-- buscador -->
 <b-row>

    <b-col class="col-buscar">

      <b-form inline>
    
      <b-form-input list="my-list-id"></b-form-input>

    <datalist id="my-list-id">
      <option v-for="state in states " :key="state.state"> {{ state.state }}</option>
    </datalist>
      
      <b-button class="btn-buscar px-5" variant="primary"><b-icon icon="search"></b-icon></b-button>

    </b-form>

    </b-col>

  </b-row>


 <b-container>

    <b-row class="py-5">
    

    <h2 class="pb-4 mh-heading--bottom-separator">Propiedades</h2>

    <b-col 
    class="col-sm-6 col-md-4 p-2"
    v-for="inm in inmuebles"
    :key="inm.id"
    >

      <div class="card ">
        <img 
        :src="url + inm.inmueble_imagenes[0].url"
        style="width:100%" 
        />
        <b-badge class="bnew" variant="primary">{{ inm.tipo_negocio.tipo }}</b-badge>
      <div class="container-card">
        <h4><b>{{ inm.titulo_inmueble }}</b></h4>
        <p class="ubicacion"><b-icon icon="geo-alt"> </b-icon> {{inm.direccion}} </p>
        <p class="precio">Precio: ${{ inm.precio_venta | priceFormattin }}</p>
        <b-row class="detalle">
          <b-col><p><img :src="require('../assets/iconos/regla.png')" style="width:24px" />: {{inm.area_total}}  </p></b-col>
          <b-col><p><img :src="require('../assets/iconos/ducha.png')" style="width:24px" />: {{inm.banos}} </p></b-col>
          <b-col><p><img :src="require('../assets/iconos/cama.png')" style="width:24px" />: {{inm.cantidad_parqueadero}} </p></b-col>
        </b-row>
        <router-link 
        class="nav-link" 
        :to="{
          name: 'detalle',
          params: { id: inm.id },
        }">
          <b-button  class="w-100" variant="outline-naranja my-3">Detalles ></b-button>
        </router-link>
        
      </div>
    </div>

    </b-col>



<b-col cols="12">
 
  <router-link class="nav-link mt-4" to="/propiedades">
   <b-button variant="naranja" >Ver todas las Propiedades</b-button></router-link>

</b-col>


  </b-row>


 </b-container>

  
  
 <b-container>

    <b-row class="py-5">
    

    <h2 class="pb-4 mh-heading--bottom-separator">Ultimas Noticias</h2>

    <b-col v-for="blog in blogs" :key="blog.id" class="col-sm-6 col-md-4 p-2">

     <div class="card ">
        <img :src="url+blog.image" style="width:100%" />
      <div class="container-card">
        <h4><b>{{blog.titulo.substr(0,49)+'...'}}</b></h4>
        <p class=""> {{blog.descripcion.substr(0,79)}} </p>

         <router-link 
        class="nav-link" 
        :to="{
          name: 'detalleblog',
          params: { id: blog.id },
        }">
          <b-button class="w-100" variant="outline-naranja my-3">Ver mas ></b-button>
        </router-link>
        
      </div>
    </div>

    </b-col>

<b-col cols="12">
  <router-link class="nav-link" to="/blog"> <b-button variant="naranja" >Visita nuestro blog</b-button></router-link>
 
</b-col>


  </b-row>

 </b-container>

</b-container>

</div>
</template>

<script>

import axios from "axios";
import { URL_LOCAL } from "../config.js";

export default {
   data() {
      return {
        url: URL_LOCAL + "storage/",
        configuracion: [],
        blog: [],
        slide: 0,
        sliding: null,
        states: [
          { state: 'Bogota' },
          { state: 'Cucuta' },
          { state: 'Medellin' },
          { state: 'Cali' }
        ],
        props: {
          msg: String
        },
      }
   },

  computed:{

    inmuebles(){
      return this.$store.state.inmuebles
    },
    usuario(){
      return this.$store.state.usuario
    },

    blogs(){

     //  lista.sort(function() {return Math.random() - 0.5})
      return this.$store.state.blogs.data.data.blogs.slice(0,3)
    },

    banners(){
      return this.$store.state.banners
    },

    code(){
      return this.$store.state.code
    },

     cssVars() {
      return {
        '--bg-color-primario': this.$store.state.color.primario,
        '--bg-color-secundario': this.$store.state.color.secundario,
      }
    }


  },

  created() {


    if (this.$route.params.code) {

      this.$store.dispatch('getCode', this.$route.params.code)
      .then(()=>this.loading=false);
      
    } 

     // this.getInmuebles();

      this.$store.dispatch('getInmuebles', this.code)
      .then(()=>this.loading=false)

      this.$store.dispatch('getBlogs', this.code)
      .then(()=>this.loading=false)

       this.$store.dispatch('getBanners', this.code)
      .then(()=>this.loading=false)

  },

  filters: {
    priceFormattin: function (value) {
      let format = parseInt(value);
      let dollarUSLocale = Intl.NumberFormat("es-CO");
      let price = dollarUSLocale.format(format);
      return price;
    },
  },

  
  methods: {
    onSlideStart(slide) {
      this.sliding = true;
      this.slide=slide;
    },
    onSlideEnd(slide) {
      this.sliding = false;
      this.slide=slide;

    },
    getInmuebles(){
      axios.get(URL_LOCAL+"api/inmueble/4245c4a2-f949-4aa1-bb0c-491448343bbc").then((result) => {
        this.inmuebles=result.data.data.inmuebles;
        this.usuario=result.data.data.userdata;
        this.configuracion=result.data.data.configuracion;
        console.log(result.data.data);
        console.log(result.data.data.configuracion);
        console.log(result.data.data.userdata);
        console.log(result.data.data.inmuebles);
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
