<script>
import { URL_LOCAL } from "../config.js";

export default {
   data() {
      return {
        url: URL_LOCAL + "storage/",
        configuracion: [],
        banners: [],
        blog: [],
        slide: 0,
        sliding: null,
        states: [
          { state: 'Bogota' },
          { state: 'Cucuta' },
          { state: 'Medellin' },
          { state: 'Cali' }
        ],
        props: {
          msg: String
        },
      }
   },

  computed:{

    inmuebles(){
      return this.$store.state.inmuebles
    },
    usuario(){
      return this.$store.state.usuario
    },

    blogs(){
      return this.$store.state.blogs
    },
    
    code(){
      return this.$store.state.code
    },
    cssVars() {
      return {
        '--bg-color-primario': this.$store.state.color.primario,
        '--bg-color-secundario': this.$store.state.color.secundario,
      }
    }
  },

  created() {
     // this.getInmuebles();

     if (this.$route.params.code) {

      this.$store.dispatch('getCode', this.$route.params.code)
      .then(()=>this.loading=false);
      
    } 

     // this.getInmuebles();

      this.$store.dispatch('getInmuebles', this.code)
      .then(()=>this.loading=false)

      this.$store.dispatch('getBlogs', this.code)
      .then(()=>this.loading=false)

       this.$store.dispatch('getBanners', this.code)
      .then(()=>this.loading=false)

  },
  
  methods: {
    onSlideStart(slide) {
      this.sliding = true;
      this.slide=slide;
    },
    onSlideEnd(slide) {
      this.sliding = false;
      this.slide=slide;

    }
  }
}
</script>

