<template>
  <div>
    <b-container fluid :style="cssVars"> 

     

 <!-- Carrusel -->
<b-row>
  <b-col class="p-5 bg-secundario" style="">
    <h2>{{blog.data.blogs.titulo}} </h2>
    <p>  {{blog.data.blogs.descripcion}}</p>
  </b-col>
</b-row>

<b-container style="text-align: left;">
  <b-row class="py-5">
    <b-col cols="8">

      <img :src="url+blog.data.blogs.image" style="width:100% margin-top:1em;" />

      <div v-html="blog.data.blogs.contenido"></div>

    </b-col>
  </b-row>
</b-container>


  <b-container v-if="blogs.data">

    <b-row class="py-5">
    

    <h2 class="pb-4 mh-heading--bottom-separator">Ultimas Noticias</h2>

    <b-col  v-for="blog in blogs" :key="blog.id" class="col-sm-6 col-md-4 p-2">
<div class="card ">
        <img :src="url+blog.image" style="width:100%" />
      <div class="container-card">
        <h4><b>{{blog.titulo.substr(0,49)+'...'}}</b></h4>
        <p class=""> {{blog.descripcion.substr(0,79)}} </p>
        <router-link 
        class="nav-link" 
        :to="{
          name: 'detalleblog',
          params: { id: blog.id },
        }">
          <b-button class="w-100" variant="outline-naranja my-3">Ver mas ></b-button>
        </router-link>
      </div>
    </div>

    </b-col>

  </b-row>

 </b-container>


</b-container>

</div>
</template>

<script>

import { URL_LOCAL } from "../config.js";


export default {
  data() {
      return {
        id:this.$route.params.id,
        url:URL_LOCAL+'/storage/',
        
        form: {
            mensaje: '',
            nombre: '',
            email: '',
            telefono: '',
            banos: '',
            estacionamiento: '',
            tipo: null,
            estrato: null,
            tipos: [
                { value: '1', text: 'Casa' },
                { value: '2', text: 'Apartamento' },
                { value: '3', text: 'Terreno' },
                { value: '4', text: 'Local', disabled: true }
            ],
            estratos: [
                { value: '1', text: '1' },
                { value: '2', text: '2' },
                { value: '3', text: '3' },
                { value: '4', text: '4' },
                { value: '5', text: '5' },
                { value: '6', text: '6' },
            ],
        },
        slide: 0,
        sliding: null,
        states: [
          { state: 'Bogota' },
          { state: 'Cucuta' },
          { state: 'Medellin' },
          { state: 'Cali' }
        ],
        props: {
          msg: String
        },
      }
  },
  computed:{


    inmuebles(){
      return this.$store.state.inmuebles
    },

    inmueblesRelacionados() {
      return this.$store.state.inmuebles.slice(0,3);
    },
    usuario(){
      return this.$store.state.usuario
    },
    
    inmueble(){
      return this.$store.state.inmueble
    },
    blogs(){
      return this.$store.state.blogs.data.data.blogs.slice(0,3)
    },

    blog(){
      return this.$store.state.blog
    },
  cssVars() {
      return {
        '--bg-color-primario': this.$store.state.color.primario,
        '--bg-color-secundario': this.$store.state.color.secundario,
      }
    }


  },

   created() {
      this.$store.dispatch('getBlog',  this.$route.params.id)
      .then(()=>this.loading=false);

      this.$store.dispatch('getInmuebles')
      .then(()=>this.loading=false);
  },

  
  
  
  methods: {
    onSlideStart(slide) {
      this.sliding = true;
      this.slide=slide;
    },
    onSlideEnd(slide) {
      this.sliding = false;
      this.slide=slide;

    }
  }
}
</script>

