<template>
  <div>
    <b-container fluid :style="cssVars"> 

     

 <!-- Carrusel -->
<b-row >
  <b-col class="p-5 header-inicio" >
    <h2>{{inmueble.titulo_inmueble}} </h2>
    <p><b-icon icon="geo-alt"> </b-icon> {{inmueble.direccion}}</p>
  </b-col>
</b-row>

<b-container>
  <b-row class="py-5">
    <b-col cols="8">

       <b-carousel
      id="carousel-1"
      v-model="slide"
      :interval="4000"
      controls
      indicators
      background="#ababab"
      img-width="1024"
      img-height="320"
      style="text-shadow: 1px 1px 2px #333;"
      @sliding-start="onSlideStart"
      @sliding-end="onSlideEnd"
      v-if="inmueble.inmueble_imagenes"
    >

      <!-- Text slides with image -->
      

      <!-- Slides with custom text -->
      

      <!-- Slides with image only -->

      <!-- Slides with img slot -->
      <!-- Note the classes .d-block and .img-fluid to prevent browser default image alignment -->
      <b-carousel-slide v-for="imagen in inmueble.inmueble_imagenes" :key="imagen.id">
        <template #img>
          <img
            class="d-block img-fluid w-100"
            width="1024"
            height="480"
            :src="url+imagen.url"
            alt="image slot"
          >
        </template>
      </b-carousel-slide>

    

      <!-- Slide with blank fluid image to maintain slide aspect ratio -->
     
    </b-carousel>


    <b class="row mt-4 p-4 bgsecundario" style=" text-align:left;">
      <b-col>
        <p v-if="inmueble.tipo_inmueble!=null">Tipo de Propiedad: {{inmueble.tipo_inmueble.tipo}}</p>
        <p v-if="inmueble.ciudad_id!=null">Ciudad: {{inmueble.ciudad_id.name}}</p>
        <p v-if="inmueble.estado_id!=null">Departamento: {{inmueble.estado_id.name}}</p>
        <p>Tamaño de la propiedad: {{inmueble.area_total}} </p>
      </b-col>
      <b-col>
        <p v-if="inmueble.tipo_negocio!=null">Tipo de Oferta: {{inmueble.tipo_negocio.descripcion}}</p>    
        <p>Zona Postal: 111011</p>
        <p>Dirección: {{inmueble.direccion}} </p>
        <p v-if="inmueble.antiguedad!=null">Año: {{inmueble.antiguedad.name}}</p>
      </b-col>

    </b>


 <b class="row mt-4 p-4 caja" style="">
      <b-col>
        <h3 class="titulo-caja">Detalles</h3>
        <p>{{inmueble.descripcion}}</p>
      </b-col>

    </b>

    <b v-if="inmueble.caracteristicas_externas" class="row mt-4 p-4 bgsecundario" style=" text-align:left;">
      <h3 class="titulo-caja">Caracteristicas Externas</h3>
      <b-col   >

        <p  
        v-for="ce in inmueble.caracteristicas_externas"      
        :key="ce.id"> <b-icon icon="check"></b-icon> {{ce.caracteristicas_externas.texto}} </p>    
      
      </b-col>

    </b>

    <b v-if="inmueble.caracteristicas_internas" class="row mt-4 p-4 bgsecundario" style=" text-align:left;">
      <h3 class="titulo-caja">Caracteristicas Internas</h3>
      <b-col   >

        <p  
        v-for="ci in inmueble.caracteristicas_internas"      
        :key="ci.id"> 
          <span v-if="ci.caracteristicas_internas"><b-icon icon="check"></b-icon> {{ci.caracteristicas_internas.texto}}</span>
        </p>    
      
      </b-col>

    </b>

    </b-col>
    <b-col>

      <h2 class="pb-4 mh-heading--bottom-separator">Información</h2>

      <div>
         <b-alert class="naranja" show variant="light" >{{inmueble.precio_venta}}</b-alert>
         <b-alert class="naranja" show variant="light">{{inmueble.direccion}} </b-alert>
         <b-alert class="naranja" show variant="light" v-if="inmueble.ciudad_id">{{inmueble.ciudad_id.name}}  </b-alert>
         <b-alert class="naranja" show variant="light">{{usuario.celular_movil}} </b-alert>
      </div>
        
        
      

      <hr>

      <h2 class="pb-4 mh-heading--bottom-separator">Contactar</h2>

        <b-form style="text-aling:left">
                 <b-form-group
                    id="input-group-1"
                >
                    <b-form-input
                    id="input-1"
                    v-model="form.nombre"
                    type="text"
                    placeholder="Nombre"
                    required
                    ></b-form-input>
                </b-form-group>

                <b-form-group
                    id="input-group-1"
                >
                    <b-form-input
                    id="input-1"
                    v-model="form.email"
                    type="text"
                    placeholder="Email"
                    required
                    ></b-form-input>
                </b-form-group>

                 <b-form-group
                    id="input-group-1"
                >
                    <b-form-input 
                    class="mb-4"
                    id="input-1"
                    v-model="form.telefono"
                    type="text"
                    placeholder="Telefono"
                    required
                    ></b-form-input>
                </b-form-group>


                <div>
                  <b-form-textarea
                    id="textarea"
                    v-model="form.mensaje"
                    placeholder="Escriba su mensaje"
                    rows="3"
                    max-rows="6"
                  ></b-form-textarea>

                </div>


             </b-form>  

             <b-row>
              <b-col class="botones mt-2">
                <b-button class=""  @click="sendContacto()" variant="naranja">Enviar</b-button>
              </b-col>
            </b-row>

            <b-row v-if="form.mensaje_contacto">
              <b-col>
                 <b-alert variant="success" show>{{form.mensaje_contacto}}</b-alert>
              </b-col>
            </b-row>
             
             <!--hr>

            <h2 class="pb-4 mh-heading--bottom-separator">Ubicación</h2>

            <b-row>

              <b-col>

                <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d18405.301648618995!2d-76.53357097891671!3d3.4433594962954466!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x8e30a6f0cc4bb3f1%3A0x1f0fb5e952ae6168!2sCali%2C%20Valle%20del%20Cauca%2C%20Colombia!5e0!3m2!1ses!2sve!4v1641900554029!5m2!1ses!2sve" width="100%" height="450" style="border:0;" allowfullscreen="" loading="lazy"></iframe>
              </b-col>
            </b-row-->


             <b-container v-if="inmueble.latitud && inmueble.longitud " class="mt-4">
                
                <h2 class="pb-4 mh-heading--bottom-separator">Ubicación</h2>

                <GmapMap :center="center" :zoom="18" style="width: 100%; height: 400px">
                  <GmapMarker
                    :key="index"
                    v-for="(m, index) in markers"
                    :position="m.position"
                    @click="center = m.position"
                  />
                </GmapMap>
              </b-container>





      
    </b-col>
  </b-row>
</b-container>

 <b-container>

    <b-row class="py-5">
    

    <h2 class="pb-4 mh-heading--bottom-separator">Propiedades Relacionadas</h2>

     <b-col 
    class="col-sm-6 col-md-4 p-2"
    v-for="inm in inmueblesRelacionados"
    :key="inm.id"
    >

      <div class="card ">
        <img 
        :src="url + inm.inmueble_imagenes[0].url"
        style="width:100%" 
        />
        <b-badge class="bnew" variant="primary">{{ inm.tipo_negocio.tipo }}</b-badge>
      <div class="container-card">
        <h4><b>{{ inm.titulo_inmueble }}</b></h4>
        <p class="ubicacion"><b-icon icon="geo-alt"> </b-icon> {{inm.direccion}} </p>
        <p class="precio">Precio: ${{ inm.precio_venta  }}</p>
        <b-row class="detalle">
          <b-col><p><img :src="require('../assets/iconos/regla.png')" style="width:24px" />: {{inm.area_total}}  </p></b-col>
          <b-col><p><img :src="require('../assets/iconos/ducha.png')" style="width:24px" />: {{inm.banos}} </p></b-col>
          <b-col><p><img :src="require('../assets/iconos/cama.png')" style="width:24px" />: {{inm.cantidad_parqueadero}} </p></b-col>
        </b-row>
        <router-link 
        class="nav-link" 
        :to="{
          name: 'detalle',
          params: { id: inm.id },
        }">
          <b-button  class="w-100" variant="outline-naranja my-3">Detalles ></b-button>
        </router-link>
        
      </div>
    </div>

    </b-col>


<b-col cols="12 mt-4">
 <b-button variant="naranja" >Ver todas las Propiedades</b-button>
</b-col>

    
 

  </b-row>




 </b-container>

  <!-- Listado de Propiedades  --->

 



  <b-container>

    <b-row class="py-5">
    

    <h2 class="pb-4 mh-heading--bottom-separator">Ultimas Noticias</h2>

    <b-col v-for="blog in blogs" :key="blog.id" class="col-sm-6 col-md-4 p-2">

      <div class="card ">
        <img :src="url+blog.image" style="width:100%" />
      <div class="container-card">
        <h4><b>{{blog.titulo.substr(0,49)+'...'}}</b></h4>
        <p class=""> {{blog.descripcion.substr(0,79)}} </p>
        <router-link 
        class="nav-link" 
        :to="{
          name: 'detalleblog',
          params: { id: blog.id },
        }">
          <b-button class="w-100" variant="outline-naranja my-3">Ver mas ></b-button>
        </router-link>
      </div>
    </div>

    </b-col>

  </b-row>

 </b-container>


</b-container>

</div>
</template>

<script>

import { URL_LOCAL } from "../config.js";


export default {
  data() {
      return {
        id:this.$route.params.id,
        url:URL_LOCAL+'/storage/',
        currentPlace: null,
        form: {
            mensaje: '',
            nombre: '',
            email: '',
            telefono: '',
            mensaje_contacto: '',
            banos: '',
            estacionamiento: '',
            tipo: null,
            estrato: null,
            tipos: [
                { value: '1', text: 'Casa' },
                { value: '2', text: 'Apartamento' },
                { value: '3', text: 'Terreno' },
                { value: '4', text: 'Local', disabled: true }
            ],
            estratos: [
                { value: '1', text: '1' },
                { value: '2', text: '2' },
                { value: '3', text: '3' },
                { value: '4', text: '4' },
                { value: '5', text: '5' },
                { value: '6', text: '6' },
            ],
        },
        slide: 0,
        sliding: null,
        states: [
          { state: 'Bogota' },
          { state: 'Cucuta' },
          { state: 'Medellin' },
          { state: 'Cali' }
        ],
        props: {
          msg: String
        },
      }
  },
  computed:{

    markers(){
      return this.$store.state.markers
    },

    center(){
      return this.$store.state.center
    },



    inmuebles(){
      return this.$store.state.inmuebles
    },

    inmueblesRelacionados() {
      return this.$store.state.inmuebles.slice(0,3);
    },
    usuario(){
      return this.$store.state.usuario
    },
    
    inmueble(){
      return this.$store.state.inmueble
    },

    blogs(){
      return this.$store.state.blogs.data.data.blogs.slice(0,3)
    },
    cssVars() {
      return {
        '--bg-color-primario': this.$store.state.color.primario,
        '--bg-color-secundario': this.$store.state.color.secundario,
      }
    }


  },

   created() {

      this.$store.dispatch('getInmueble',  this.$route.params.id)
      .then(()=>this.loading=false);

      this.$store.dispatch('getInmuebles')
      .then(()=>this.loading=false);

  },

  
  
  
  methods: {
    onSlideStart(slide) {
      this.sliding = true;
      this.slide=slide;
    },
    onSlideEnd(slide) {
      this.sliding = false;
      this.slide=slide;

    },
    sendContacto() {
      
      let formData = new FormData();

      formData.append("user_id", this.usuario.id);
      formData.append("full_name", this.form.nombre);
      formData.append("email", this.form.email);
      formData.append("celular", this.form.telefono);
      formData.append("message", this.form.mensaje);

      console.log(formData);

        this.$store
          .dispatch("addContacto", formData)
          .then((response) => {
            this.form.mensaje_contacto='Mensaje Enviado satisfactoriamente';
            this.form.nombre='';
            this.form.email='';
            this.form.mensaje='';
            this.form.telefono='';
           // this.$router.push({ name: "banners-list" });
           console.log(response)
          })
          .catch((error) => {
           console.log(error)
          });
      },

      



  }
}
</script>

